'use server';
/**
 * @fileOverview Generates a rationale for why a coaching option is a good or bad fit for a student.
 *
 * - generateCoachingRationale - A function that generates the coaching rationale.
 * - GenerateCoachingRationaleInput - The input type for the generateCoachingRationale function.
 * - GenerateCoachingRationaleOutput - The return type for the generateCoachingRationale function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const GenerateCoachingRationaleInputSchema = z.object({
  studentTraits: z
    .array(z.string())
    .describe('The personality traits of the student.'),
  coachingTags: z.array(z.string()).describe('The tags associated with the coaching.'),
});
export type GenerateCoachingRationaleInput = z.infer<
  typeof GenerateCoachingRationaleInputSchema
>;

const GenerateCoachingRationaleOutputSchema = z.object({
  goodFitRationale: z
    .string()
    .describe('A concise explanation for why the coaching is a good fit for the student.'),
  badFitRationale: z
    .string()
    .describe(
      'A concise explanation for why the coaching might not be a good fit, presenting a potential downside or alternative to consider.'
    ),
});
export type GenerateCoachingRationaleOutput = z.infer<
  typeof GenerateCoachingRationaleOutputSchema
>;

export async function generateCoachingRationale(
  input: GenerateCoachingRationaleInput
): Promise<GenerateCoachingRationaleOutput> {
  return generateCoachingRationaleFlow(input);
}

const prompt = ai.definePrompt({
  name: 'generateCoachingRationalePrompt',
  input: {schema: GenerateCoachingRationaleInputSchema},
  output: {schema: GenerateCoachingRationaleOutputSchema},
  prompt: `You are an AI assistant that provides explanations for why a particular coaching option may or may not be a good fit for a student, given the student's personality traits and the coaching's attributes.

  Student Traits: {{studentTraits}}
  Coaching Tags: {{coachingTags}}

  Provide a concise explanation for why the coaching option is a good fit and one for why it might be a bad fit, referencing specific traits and tags.
  - Good Fit: Explain the positive alignment.
  - Bad Fit: Explain a potential drawback or a point of friction.`,
});

const generateCoachingRationaleFlow = ai.defineFlow(
  {
    name: 'generateCoachingRationaleFlow',
    inputSchema: GenerateCoachingRationaleInputSchema,
    outputSchema: GenerateCoachingRationaleOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
